<template>
  <div>
    <h1>Profile</h1>
    <button @click="getBirth">得到生日</button>
    <p>你的生日是:{{ birth }}</p>
  </div>
</template>

<script>
import request from "@/network/netUtils.js";

export default {
  data() {
    return {
      birth: "",
    };
  },
  methods: {
    getBirth() {
      const config = {
        url: "api/profile",
        method: "get",
        headers: {
          authorization: "Bearer " + document.cookie,
        },
      };
      console.log(config.headers.authorization);
      request(config)
        .then((res) => {
          console.log(res.data);
          this.birth = res.data.birth;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

