<template>
  <!-- 杂志卡片区 -->
  <div id="card-box">
    <journal-card
      v-for="imageInfo in imageInfos"
      :key="imageInfo.id"
      :image-info="imageInfo"
    ></journal-card>
  </div>
</template>

<script>
import JournalCard from "@/components/JournalCard";
export default {
  data() {
    return {};
  },
  props: {
    columnKind: String,
  },
  methods: {},
  computed: {
    imageInfos() {
      if (this.columnKind === "recommand") {
        return [
          { url: "1.webp", id: "1" },
          { url: "2.webp", id: "2" },
          { url: "3.webp", id: "3" },
          { url: "4.webp", id: "4" },
          { url: "5.webp", id: "5" },
          { url: "6.webp", id: "6" },
          { url: "7.webp", id: "7" },
          { url: "8.webp", id: "8" },
        ];
      } else if (this.columnKind === "latest") {
        return [
          { url: "9.webp", id: "9" },
          { url: "10.webp", id: "10" },
          { url: "11.webp", id: "11" },
          { url: "12.webp", id: "12" },
          { url: "13.webp", id: "13" },
          { url: "14.webp", id: "14" },
          { url: "15.webp", id: "15" },
          { url: "16.webp", id: "16" },
        ];
      } else if (this.columnKind === "eng") {
        return [{ url: "17.webp", id: "17" }];
      } else if (this.columnKind === "gym") {
        return [{ url: "18.webp", id: "18" }];
      } else if (this.columnKind === "it") {
        return [{ url: "19.webp", id: "19" }];
      } else if (this.columnKind === "game") {
        return [{ url: "20.webp", id: "20" }];
      }
    },
  },
  components: {
    JournalCard,
  },
};
</script>

